/* modules */
.header {
  &.--new {
    font-size: 16px;
    h1, .h1 {
      font-size: 50px;
      margin: 0 0 25px 0;
      text-align: left;
      line-height: 1;
      .fs60 {
        font-size: 60px !important;
      }
    }
    h2, .h2 {
      color: #fff;
      font-size: 30px;
      margin: 0 0 25px 0;
      text-align: left;
      line-height: 1.2;
    }
    p {
      line-height: 1.3;
      margin: 0 0 10px 0;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &::before {
      top: 0;
      height: auto;
      bottom: 0;
    }
  }
  @media (max-width: 980px) {
    &.--new {
      height: auto !important;
      padding: 50px 0;
      .h1, h1 {
        text-align: center;
      }
      .h2, h2 {
        text-align: center;
      }
    }
  }
  @media (max-width: 600px) {
    &.--new {
      h1, .h1 {
        margin-bottom: 15px;
        .fs60 {
          font-size: 50px !important;
        }
      }
      h2, .h2 {
        margin-bottom: 15px;
        font-size: 23px;
      }
    }
  }
}

.d-block {
  display: block !important;
}

.section-content {
  padding-top: 117px;
  &__content {
    text-align: left;
    font-size: 14px;
    margin-bottom: 45px;
    h1, .h1 {
      font-size: 40px;
      margin: 0 0 25px 0;
      font-weight: 700;
    }
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  @media (max-width: 1199px) {
    padding-top: 80px;
  }
}

.bordered-blocks {
  &.--new {
    .bordered-item {
      padding-top: 25px;
      padding-bottom: 25px;
      .btn {
        max-width: 263px;
        font-size: 16px;
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
      }
      .img-block {
        top: -50px;
      }
    }
  }
  &.--new2 {
    max-width: 1038px;
    margin-left: auto;
    margin-right: auto;
    .bordered-item {
      padding-top: 21px;
      padding-bottom: 21px;
      margin-right: 139px;
      margin-bottom: 40px;
      .btn {
        max-width: 263px;
        font-size: 16px;
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
      }
      .text-block {
        h3, .h3 {
          margin-bottom: 37px;
          max-width: 375px;
        }
        .--color {
          color: #F58971;
          font-size: 24px;
        }
      }
      .img-wrapper {
        img {
          height: 240px;
          width: 570px;
          max-width: inherit;
          object-fit: cover;
          clip-path: polygon(0 0, 100% 0, calc(100% - 38px) 100%, 0% 100%);
        }
      }
      &:nth-child(2n) {
        margin-left: 0;
        padding-left: 30px;
      }
    }
  }
  @media (max-width: 1199px) {
    &.--new2 {
      .bordered-item {
        .img-wrapper {
          img {
            width: 524px;
          }
        }
      }
    }
  }
  @media (max-width: 991px) {
    &.--new {
      .bordered-item {
        .img-block {
          top: 0;
          margin-top: 25px;
        }
        &:nth-child(2n) {
          .img-block {
            margin-top: 0px;
            margin-bottom: 25px;
          }
        }
      }
    }
    &.--new2 {
      .bordered-item {
        margin-right: 0;
        &>.row {
          &>div {
            width: 100%;
          }
        }
        .text-block {
          h3, .h3 {
            max-width: inherit;
          }
        }
        .img-wrapper {
          margin-top: 25px;
          img {
            width: 100%;
            height: auto;
            clip-path: none;
          }
        }
      }
    }
  }
}

.order-prayer {
  background: #FAFAFA;
  padding: 70px 0 70px 40px;
  margin-left: -15px;
  box-shadow: 450px 0 0 #fafafa;
  &__input {
    display: flex;
    &>div {
      &+div {
        margin-left: 35px;
      }
    }
  }
  &__btn {
    background: none;
    border: 0;
    padding: 0;
    color: #333333;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: bold;
    margin: 0 0 30px 0;
    line-height: normal;
    white-space: nowrap;
    .fa {
      font-size: 22px;
      margin-right: 10px;
      vertical-align: -2px;
    }
    &:hover {
      color: #F87D6C;
    }
    .--hidden {
      display: none;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    .btn {
      height: 60px;
      min-width: 300px;
      font-size: 16px;
    }
    &>div {
      &+div {
        margin-left: 30px;
      }
    }
  }
  &__price {
    font-size: 18px;
    line-height: 1;
    .span {
      display: block;
    }
    strong {
      display: block;
      line-height: 1;
      font-size: 40px;
      color: #F58971;
      white-space: nowrap;
    }
  }
  .form-checkbox.--last {
    margin-bottom: 25px;
  }
  @media (max-width: 1199px) {
    &__bottom {
      .btn {
        min-width: 240px;
      }
    }
  }
  @media (max-width: 991px) {
    box-shadow: 450px 0 0 #fafafa,-450px 0 0 #fafafa;
    margin-left: 0;
    margin-bottom: 25px;
    padding: 35px 15px;
  }
  @media (max-width: 599px) {
    &__input {
      flex-direction: column;
      padding-bottom: 15px;
      &>div {
        &+div {
          margin-left: 0;
        }
      }
      .form-group {
        margin-bottom: 5px;
      }
    }
    &__bottom {
      flex-direction: column;
      &>div {
        &+div {
          margin-left: 0;
          margin-top: 25px;
        }
      }
    }
    &__btn {
      .--visible {
        display: none;
      }
      .--hidden {
        display: inline-block;
      }
    }
  }
}

.form-control {
  &.--new {
    border-radius: 0;
    height: 40px;
    font-size: 16px;
    border: 2px solid rgba(0, 0, 0, 0.1);
    box-shadow: none;
    background: #fff;
    font-weight: 700;
    padding: 0 24px 0 24px;
    line-height: normal;
    &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
      color: #A8A8A8;
      font-size: 14px;
      font-weight: 400;
    }
    &::-moz-placeholder { /* Firefox 19+ */
      color: #A8A8A8;
      font-size: 14px;
      font-weight: 400;
    }
    &:-ms-input-placeholder { /* IE 10+ */
      color: #A8A8A8;
      font-size: 14px;
      font-weight: 400;
    }
    &:-moz-placeholder { /* Firefox 18- */
      color: #A8A8A8;
      font-size: 14px;
      font-weight: 400;
    }
    &[aria-invalid=false] {
      border-color: #FFBA5B !important;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.1);
    }
  }
}

.form-checkbox {
  &.--new {
    line-height: normal;
    margin-bottom: 10px;
    margin-left: 0 !important;
    vertical-align: middle;
    label {
      display: inline-block;
      cursor: pointer;
      font-size: 14px;
      line-height: normal;
      min-height: 20px;
      padding-top: 2px;
      padding-left: 30px;
      &::before {
        border: 1px solid #F87D6C;
        background: #fff;
        top: 0;
      }
    }
    input {
      &:checked {
        &~label {
          &::before {
            border-width: 5px;
          }
        }
      }
    }
  }
}

.j-slider {
  .slick-slide {
    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }
  .slick-next, .slick-prev {
    right: 0;
    bottom: 0;
    width: 30px;
    height: 30px;
    background: #F58971;
    opacity: 1;
    top: auto;
    margin: 0;
    padding: 0;
    text-align: center;
    line-height: 30px;
    color: #fff;
    font-family: 'FontAwesome';
    text-indent: 0;
    font-size: 0;
    &::before {
      content: "\f105";
      font-size: 20px;
    }
    &:hover {
      opacity: 0.7 !important;
    }
  }
  .slick-prev {
    right: 36px;
    left: auto;
    &::before {
      content: "\f104";
    }
  }
}

.page-nav {
  &.--new {
    .page-nav-pages {
      text-align: left;
      font-size: 14px;
      &>a, &>span {
        display: inline-block;
        text-decoration: none;
        margin: 0;
        border: 0;
        &+* {
          margin-left: 30px;
        }
        &.active {
          font-size: 22px;
          color: #F58971;
        }
      }
    }
    .page-nav__count {
      text-align: right;
      font-size: 14px;
      b {
        font-size: 16px;
      }
    }
    @media (max-width: 599px) {
      .page-nav-pages {
        text-align: center;
      }
      .page-nav__count {
        text-align: center;
        margin-top: 25px;
      }
    }
  }
}

.services {
  &__block {
    display: flex;
    font-size: 14px;
    margin-bottom: 60px;
    align-items: flex-start;
    &-thumb {
      flex-shrink: 0;
      width: 360px;
      position: relative;
      img {
        clip-path: polygon(0 0, 100% 0, calc(100% - 34px) 100%, 0% 100%);
        height: 220px;
        width: 100%;
        object-fit: cover;
      }
      .services__block-label {
        top: auto;
        left: auto;
        right: 0;
        bottom: 0;
        width: 100px;
        div {
          top: 100%;
          margin-top: 3px;
          left: 100%;
          margin-left: -22px;
          transform: rotate(-90deg);
        }
      }
    }
    &-title {
      margin-bottom: 22px;
      font-size: 26px;
      font-weight: bold;
    }
    &-content {
      flex-grow: 1;
      padding: 0 30px;
      p {
        line-height: 1.7;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &-last {
      width: 200px;
      flex-shrink: 0;
      position: relative;
    }
    &-price {
      position: relative;
      z-index: 7;
      height: 220px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 18px;
      background: #FAFAFA;
      clip-path: polygon(35px 0, 100% 0, 100% 100%, 0% 100%);
      padding-left: 35px;
      .span {
        display: block;
        line-height: 1;
      }
      strong {
        display: block;
        line-height: 1;
        white-space: nowrap;
        color: #F58971;
        font-size: 40px;
      }
    }
    &-btn {
      height: 40px;
      font-size: 16px;
      font-weight: bold;
      text-align: center;
      text-transform: uppercase;
      background: #F87D6C;
      border: 0;
      display: block;
      width: 100%;
      color: #fff;
      position: absolute;
      left: 0;
      bottom: 0;
      right: 0;
      z-index: 10;
      &:hover {
        background: #a91818;
      }
    }
    &-label {
      position: absolute;
      width: 36px;
      height: 100%;
      background: #F58971;
      font-weight: 900;
      font-size: 26px;
      text-transform: uppercase;
      color: #fff;
      top: 0;
      left: 0;
      overflow: hidden;
      div {
        transform-origin: 0 0;
        transform: rotate(90deg);
        left: 0;
        position: absolute;
        margin-left: 23px;
        top: -4px;
      }
    }
  }
  @media (max-width: 1199px) {
    &__block {
      &-thumb {
        width: 300px;
      }
    }
  }
  @media (max-width: 991px) {
    &__block {
      flex-wrap: wrap;
      &-thumb {
        margin-bottom: 25px;
        width: 100%;
        img {
          height: auto;
          width: 100%;
        }
      }
      &-content {
        width: calc(100% - 200px);
        padding-left: 0;
      }
    }
  }
  @media (max-width: 599px) {
    &__block {
      &-content {
        width: 100%;
        padding: 0;
      }
      &-last {
        margin: 25px auto 0;
        width: 100%;
      }
    }
  }
}

.blog {
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
  &__block {
    width: calc(33.3% - 30px);
    margin: 0 15px 79px 15px;
    position: relative;
    font-size: 13px;
    &-thumb {
      overflow: hidden;
      height: 200px;
      width: 100%;
      background: #eee;
      clip-path: polygon(calc(100% - 24px) 0, 100% 24px, 100% 100%, 0 100%, 0 0);
      img {
        height: 200px;
        object-fit: cover;
        display: block;
        width: 100%;
      }
      &::before {
        content: '';
        position: absolute;
        z-index: 5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba(#000,0.5);
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
      }
      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        width: 24px;
        height: 24px;
        background: #F58971;
        z-index: 10;
      }
      a {
        display: block;
      }
    }
    &-content {
      display: block;
      color: #000000 !important;
      text-decoration: none !important;
      position: absolute;
      width: 100%;
      max-width: 230px;
      padding: 13px 10px 15px 16px;
      background: #FFFFFF;
      box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
      right: 16px;
      bottom: -38px;
      min-height: 85px;
      z-index: 5;
      p {
        margin-bottom: 0;
        line-height: 1.5;
      }
    }
    &:hover {
      .blog__block {
        &-thumb {
          clip-path: none;
          &::before {
            opacity: 1;
            visibility: visible;
          }
          &::after {
            display: none;
          }
        }
        &-content {
          background: #F58971;
          color: #fff !important;
        }
      }
    }
  }
  @media (max-width: 991px) {
    &__block {
      width: calc(50% - 30px);
    }
  }
  @media (max-width: 599px) {
    &__block {
      width: calc(100% - 30px);
      &-thumb {
        height: auto;
        img {
          height: auto;
        }
      }
    }
  }
}

.col-xxs-12 {
  @media (max-width: 599px) {
    width: 100% !important;
  }
}

.blog-document {
  min-height: 300px;
  font-size: 18px;
  color: #333;
  p {
    line-height: 1.5;
  }
  &__thumb {
    float: right;
    position: relative;
    margin-left: 36px;
    margin-bottom: 50px;
    img {
      width: 295px;
      height: 248px;
      object-fit: cover;
      position: relative;
      z-index: 5;
    }
    &::before {
      width: 100%;
      height: 100%;
      position: absolute;
      content: '';
      z-index: 1;
      left: -9px;
      top: 9px;
      background: #F58971;
    }
  }
  .h1, h1, .h2, h2 {
    font-size: 30px;
    text-align: left;
    margin: 0 0 25px 0;
    color: #333;
    font-weight: 700;
  }
  .h2, h2 {
    font-size: 20px;
    line-height: 1.6;
  }
  @media (max-width: 767px) {
    &__thumb {
      float: none;
      margin: 0 auto 35px;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.share {
  display: flex;
  align-items: center;
  color: #F87D6C;
  font-size: 12px;
  &>div {
    &:first-child {
      max-width: 138px;
      line-height: 1.7;
    }
  }
  @media (max-width: 767px) {
    flex-direction: column;
    align-items: flex-start;
    &>div {
      &:first-child {
        max-width: inherit;
        margin-bottom: 15px;
      }
    }
  }
}

