  padding: 0 !important;
  font-size: 16px;
  &__body {
    padding: 50px 83px 44px 50px;
    display: flex;
    background: #fff;
  }
  &__footer {
    background: #FAFAFA;
    padding: 50px 53px 50px 50px;
    &-wrapper {
      display: flex;
      align-items: center;
      .form-checkbox {
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
    &>form {
      display: block;
      width: 100%;
    }
    .first {
      flex-grow: 1;
    }
    .last {
      flex-shrink: 0;
      margin-left: 55px;
    }
    &-btns {
      display: flex;
      align-items: center;
      .btn {
        width: 270px;
        height: 80px;
        margin-left: 22px;
        font-size: 20px;
      }
    }
    &-price {
      flex-shrink: 0;
      font-size: 18px;
      line-height: 1;
      strong {
        display: block;
        line-height: 1;
        font-size: 50px;
        color: #f58971;
        white-space: nowrap;
      }
      .span {
        display: block;
      }
    }
    &-input {
      display: flex;
      margin-bottom: 20px;
      &>div {
        width: 50%;
        &+div {
          margin-left: 30px;
        }
      }
    }
  }
  &__thumb {
    flex-shrink: 0;
    width: 330px;
    img {
      width: 100%;
      height: 260px;
      object-fit: cover;
    }
  }
  &__content {
    flex-grow: 1;
    padding-left: 30px;
    color: #333;
    .h1, h1 {
      font-size: 40px;
      margin: 0 0 25px 0;
      font-weight: bold;
      .--color {
        display: block;
        color: #F87D6C;
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 10px;
      }
    }
    p {
      line-height: 1.7;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  @media (max-width: 1199px) {
    &__footer {
      &-btns {
        .btn {
          width: 150px;
        }
      }
    }
  }
  @media (max-width: 991px) {
    &__body {
      flex-direction: column;
      padding: 30px;
    }
    &__content {
      padding-left: 0;
    }
    &__thumb {
      margin: 0 auto 25px;
    }
    &__footer {
      padding: 30px;
      &-wrapper {
        flex-direction: column;
        .last {
          margin-left: 0;
          margin-top: 25px;
        }
      }
      &-btns {
        .btn {
          width: 280px;
        }
      }
    }
  }
  @media (max-width: 599px) {
    &__body, &__footer {
      padding: 20px;
    }
    &__thumb {
      width: 100%;
      img {
        height: auto;
      }
    }
    &__footer {
      &-wrapper {
        &>div {
          width: 100% !important;
        }
      }
      &-input {
        flex-direction: column;
        &>div {
          margin: 0 !important;
          width: 100% !important;
          &+div {
            margin-top: 15px !important;
          }
        }
      }
      &-btns {
        flex-direction: column;
        .btn {
          margin-left: 0;
          margin-top: 25px;
          height: 60px;
          max-width: 260px;
        }
      }
    }
  }
}

#order-modal {
  background: rgba(248,125,108,0.5);
}

