.index-nav {
  margin-bottom: 80px;
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }
  &__block {
    margin: 0 15px 40px 15px;
    width: calc(33.3% - 30px);
    font-size: 22px;
    &-thumb {
      position: relative;
      margin-bottom: 20px;
      img {
        width: 100%;
        height: auto;
        display: block;
        position: relative;
        z-index: 5;
        &:first-child {
          position: absolute;
          z-index: 1;
          transform-origin: 50%;
          transform: rotate(2deg);
          opacity: 0.3;
        }
      }
      &::before {
        content: '';
        position: absolute;
        border: 10px solid #F58971;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        cursor: pointer;
        z-index: 15;
        background: rgba(#000,.5);
        opacity: 0;
        visibility: hidden;
      }
    }
    &-link {
      color: #333 !important;
      text-decoration: none !important;
      font-weight: bold;
      font-size: 22px;
      &:hover {
        text-decoration: underline !important;
      }
    }
    &:hover {
      .index-nav__block-thumb {
        &::before {
          opacity: 1;
          visibility: visible;
        }
        img {
          &:first-child {
            opacity: 0;
          }
        }
      }
    }
  }
